.chip {
  background: #3b4d61;
  color: white;
  border-radius: 9px;
  font-size: 0.7em;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding-left: 1em;
  padding-right: 1em;
  margin-right: 1em;
}

.top-chip-wrapper {
  display: flex;
  justify-content: center;
}

.top-chip {
  padding-top: .5em;
  padding-bottom: .8em;
  width: 6em;
  cursor: pointer;
  margin: 0px;
  border-radius: 0px 0px 0px 8px;
  box-shadow: 0 1px 2px 0 #0f0f0f;
  border-bottom: solid 2px #3b4d61;
  transition: border-bottom 0.2s ease-in-out;
}

.top-chip-right {
  border-radius: 0px 0px 8px 0px;
}

.top-chip:hover {
  border-bottom: solid 2px #fff;
  transition: border-bottom 0.2s ease-in-out;
}

