.box-1 {
    width: 1em;
    height: 0.3em;
    border-top: solid 1px #FFF;
    transition: transform 0.3s, width 0.3s, height 0.3s;
}

.box-1-open {
    width: 0.7em;
    height: 0.3em;
    border-top: solid 1px #FFF;
    transform: rotate(-35deg) translate(-0.2em, -0.03em);
    transition: transform 0.3s, width 0.3s, height 0.3s;
}

.box-2 {
    width: 1em;
    height: 0.3em;
    border-top: solid 1px #FFF;
}

.box-2-open {
    width: 1em;
    height: 0.3em;
    border-top: solid 1px #FFF;
}

.box-3 {
    width: 1em;
    height: 0.3em;
    border-top: solid 1px #FFF;
    transition: transform 0.3s, width 0.3s, height 0.3s;
}

.box-3-open {
    width: 0.7em;
    height: 0.3em;
    border-top: solid 1px #FFF;
    transition: transform 0.3s, width 0.3s, height 0.3s;
    transform: rotate(35deg) translate(-0.39em, 0.06em);
}

