@media screen and (max-width: 768px) {
  .main-wrapper {
    padding: 2rem;
    padding-top: 1em;
  }
  .top-chip-wrapper {
    margin-top: -1em;
  }
  .page-title {
    line-height: 1.1em;
  }
  .page-title-2 {
    margin-top: 0.5em;
    margin-bottom: 0.2em;
    line-height: 1.1em;
  }
  .page-title-wrapper {
    margin-top: 0;
    margin-bottom: 2em;
  }
  .page-title-wrapper-2 {
    padding-bottom: 1em;
    margin-top: 0;
    margin-bottom: 2em;
  }
  .home {
    margin-top: 0;
  }
  .page-wrapper {
    margin-top: -1em;
  }
  .center-eq {
    font-size: 0.7em;
  }

  .page-title-extras {
    display: none;
  }

  html {
    font-size: .7em;
  }

  .ann-rpd-wrapper {
    display: flex;
    flex: 1;
    flex-direction: column-reverse;
    overflow: hidden;
  }

  .ann-rpd-transcription {
    height: 50%;
    width: 100%;
  }
  
  .ann-rpd-video-container {
    height: 50%;
  }
}

.ann-rpd-video {
  clip-path: inset(1px 1px);
}

.ann-rpd-sidepanel {
